<template>
    <div>
        <v-row>
            <v-col>
                <canvas
                    height="600px"
                    width="900px"
                    ref="canvas"
                ></canvas>
            </v-col>
            <v-col>
                <div
                    v-if="message!=''"
                >{{message}}
                </div>
            </v-col>
        </v-row>
          <div class="modal-backdrop"
            v-if="endGame"
          >
            <div class="modal">
            <header class="modal-header">
                {{endMsg}}
            </header>
            <section class="modal-body">
                <slot name="body">
                    <v-btn
                        @click="toMenu"
                    >К уровням</v-btn>
                    <v-btn
                        @click="tryAgain"
                    >Попробовать снова</v-btn>
                </slot>
            </section>
            </div>
        </div>
    </div>
    
</template>

<script>
import BGImage from "../assets/lvl_1/bg.png";
import ImageCloseBtn from "../assets/lvl_1/close_btn.png";
import ImageBuyBtn from "../assets/lvl_1/buy_btn.png";


export default {
    name: 'levelOne',
    data: () => ({
        vueCanvas: null,
        clickCounter: 0,
        message: "",
        clickTo: 0,
        rndGap: 0,
        trapActivated: false,
        endGame: false,
        endMsg : '',
    }),
    created(){
    },
    methods:{
        toMenu(){
            this.$router.push({ name: 'game-start'});
            this.endGame = false;
            this.endMsg = "";
        },
        tryAgain(){
            location.reload();
        },
        init(){
            let cvn = this.$refs.canvas;
            this.vueCanvas = cvn;
            let ctx = cvn.getContext("2d"); 
            
            let buyBtn = new Image();
            let bg = new Image();
            let closeBtn = new Image();

            closeBtn.src = ImageCloseBtn;
            buyBtn.src = ImageBuyBtn;
            bg.src = BGImage;

            bg.onload = function() {
                    ctx.drawImage(bg, 0 ,0, cvn.width, cvn.height);
                    ctx.drawImage(closeBtn, cvn.width-closeBtn.width, 0);
                    ctx.drawImage(buyBtn, (cvn.width/2)-buyBtn.width/2, cvn.height-buyBtn.height);
            };

        },
        getRndInteger(min, max) {
            return Math.floor(Math.random() * (max - min)) + min;
        },
        checkClick(x,y){

            //alert("click");
            this.vueCanvas = this.$refs.canvas;
            let ctx = this.vueCanvas.getContext("2d");

            let closeBtn = new Image();
            let buyBtn = new Image();
            buyBtn.src = ImageBuyBtn;
            closeBtn.src = ImageCloseBtn;

            if (x > this.vueCanvas.width - closeBtn.width && y > 0 && y < closeBtn.height && !this.trapActivated){
                if (this.clickCounter == 0) {
                    this.clickTo = this.getRndInteger(20, 40);
                    this.rndGap = this.getRndInteger(0,4);
                    this.message = "Кликов осталось: " + this.clickTo;
                } else  if (this.clickCounter < this.clickTo - this.rndGap){
                    this.message = "Кликов осталось: " + (this.clickTo - this.clickCounter);
                } else {
                    this.message = "Кликов осталось: " + (this.clickTo - this.clickCounter);
                    ctx.drawImage(buyBtn, this.vueCanvas.width - buyBtn.width, 0);
                    this.trapActivated = true;
                    ctx.drawImage(closeBtn, 0, 0);
                } 
                this.clickCounter++;
            } else {
                if (x > (this.vueCanvas.width/2)-buyBtn.width/2 &&
                    y > this.vueCanvas.height-buyBtn.height &&
                    x < (this.vueCanvas.width/2)+buyBtn.width/2){
                    this.endGame = true;
                    this.endMsg = "Вы проиграли"
                } else if (x > this.vueCanvas.width-buyBtn.width &&
                    y > 0 && y < buyBtn.height &&
                    this.trapActivated){
                    this.endGame = true;
                    this.endMsg = "Вы проиграли"
                }
                if (x < closeBtn.width && y < closeBtn.height){
                    this.endGame = true;
                     this.endMsg = "Вы победили злобную рекламу!"
                }
            }
        },
    },
    mounted(){
        this.init();
        this.vueCanvas.addEventListener("click", event => {
            let bound = this.vueCanvas.getBoundingClientRect();
            let x = event.clientX - bound.left - this.vueCanvas.clientLeft;
            let y = event.clientY - bound.top - this.vueCanvas.clientTop;
            this.checkClick(x,y);
        });
        
    },
}
</script>

<style>
#canvas {
  border: 1px solid gray;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: #ffffff;
  box-shadow: 20px 20px 20px 20px;
  overflow-x: auto;
  display: flex;
  flex-direction: column;
}

.modal-header,
.modal-footer {
  padding: 15px;
  display: flex;
}

.modal-header {
  border-bottom: 1px solid #eeeeee;
  color: #ebac0c;
  justify-content: space-between;
}

.modal-footer {
  border-top: 1px solid #eeeeee;
  justify-content: flex-end;
}

.modal-body {
  position: relative;
  padding: 10px 10px 10px 10px;
}

.btn-close {
  border: none;
  font-size: 15px;
  cursor: pointer;
  font-weight: bold;
  color: #ebac0c;
  background: transparent;
}

.btn-func {
  color: white;
  background: #ebac0c;
  margin: 10px 20px 10px 20px;
  padding: 10px;
  border: 1px solid #ebac0c;
  border-radius: 2px;
}

</style>